import React, { Component } from 'react'

export default class Investing extends Component {
    state = {
        balance : {}
    };
    componentDidMount(){
        fetch("https://i3g96.sse.codesandbox.io/balance")
        .then(response => response.json())
        .then(( balance ) => {
            this.setState({ balance :  balance});
            console.log("REMAING BALANCE",balance);  
        });
        
    }
    
    renderBalance(){
		return Object.values(this.state.balance).map((myBalance) => {
			return (
				<li key={myBalance.value}>
					Welcome !{myBalance.name}
					<h4>Remaining balance:</h4> ${myBalance.amount.toFixed()}
				</li>
			)
		})
	}
    render() {
        return (
            <div>
            <ul>{this.renderBalance()}</ul>
            </div>
        )
    }
}
